<template>
  <div>
    <h1>Wave Buttons</h1>
    <div>
      <button class="qr-button wave-button">Wave Effect</button>
      <div class="text-g" style="padding-left:100px">
        <span class="txt anim-text-flow">How are you? Bacon ipsum dolor sit amet.</span>
      </div>
    </div>
  </div>
</template>

<style scoped>
/* Common button styles */
.qr-button {
  padding: 15px 30px;
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
  margin: 10px;
  background: linear-gradient(45deg, #0077ff, #00ccff);
  position: relative;
  overflow: hidden;
}

/* Wave Button */
.wave-button {
  background: linear-gradient(45deg, #0077ff, #00ccff);
}

.wave-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300%;
  height: 300%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0) 70%);
  transition: transform 0.5s ease, opacity 0.5s ease;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
}

.wave-button:hover::before {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}

/* Extra Wave Effect */
.wave-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: -45%;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0) 70%);
  transition: transform 0.5s ease;
  transform: translateX(-100%) skewX(-15deg);
}

.wave-button:hover::after {
  transform: translateX(200%) skewX(-15deg);
}

@import url('https://fonts.googleapis.com/css?family=Ubuntu:300');
</style>
